import './style.scss';
import classNames from 'classnames';
import Swipeable from 'react-swipeable';


export default class extends React.Component{
  static propTypes = {
    cssClass:React.PropTypes.string,
    items:React.PropTypes.array,
    duration:React.PropTypes.number,
    animate:React.PropTypes.string,
    itemTemplate:React.PropTypes.func,
  };

  static defaultProps = {
    items:[],
    duration:0.3,
    animate:'linear',
    activeIndex:0,
    itemTemplate:function(item,activeIndex){
      return (<div>{item}</div>);
    }
  };

  constructor(props){
    super(props);
    this.fomratItems();
    this.state={
      activeIndex:props.activeIndex,
      dimensions:{},
      translateX:0,
      duration:props.duration
    };

  }

  componentWillMount(){
    this.toIndex(this.state.activeIndex+1);
  }

  componentDidMount() {
    var root = this._root = this.refs.root;
    this._bound = root.getBoundingClientRect();
  }

  fomratItems(){
    var items = this.props.items;
    var first = items[0];
    var last = items[items.length - 1];
    items.push(first);
    items.unshift(last);
  }

  toIndex(inIndex){
    var activeIndex = this.getAbsIndex(inIndex);
    this.setState({
      activeIndex:activeIndex,
      duration:this.props.duration,
      translateX:`-${inIndex * 100/this.props.items.length}%`
    });
  }

  _onSwipedLeft(ev){
    var activeIndex = this.state.activeIndex + 1;
    this.toIndex(activeIndex);
  }

  _onSwipedRight(ev){
    var activeIndex = this.state.activeIndex - 1;
    this.toIndex(activeIndex);
  }

  _onSwipingLeft(ev,deltaX){
    var _translateX = this.state.activeIndex * this._bound.width;
    this.setState({
      duration:0,
      translateX:`-${_translateX+deltaX}px`
    });
  }

  _onSwipingRight(ev,deltaX){
    var _translateX = this.state.activeIndex * this._bound.width;
    this.setState({
      duration:0,
      translateX:`-${_translateX-deltaX}px`
    });
  }

  _onTransitionEnd(){
    var activeIndex = this.getAbsIndex(this.state.activeIndex);
    this.setState({
      activeIndex:activeIndex,
      duration:0,
      translateX:`-${activeIndex * 100/this.props.items.length}%`
    });
  }

  getAbsIndex(inIndex){
    var activeIndex = inIndex;
    var length = this.props.items.length;
    var _length = length - 1;

    if(activeIndex == 0){
      activeIndex = _length - 1;
    }

    if(activeIndex == _length){
      activeIndex = 1;
    }

    return activeIndex;
  }


  render(){
    return (
      <div ref="root" className={classNames('react-swipe-views',this.props.cssClass)}>
        <Swipeable flickThreshold={0.2} delta={10} preventDefaultTouchmoveEvent className="react-swipe-views-wrapper"
          onSwipingLeft={this._onSwipingLeft.bind(this)}
          onSwipingRight={this._onSwipingRight.bind(this)}
          onSwipedLeft={this._onSwipedLeft.bind(this)}
          onSwipedRight={this._onSwipedRight.bind(this)}
          >
            <div className="react-swipe-views-scroller"
              style={{
                width:`${this.props.items.length*100}%`,
                transition:`transform ${this.state.duration}s ${this.props.animate}`,
                transform:`translateX(${this.state.translateX})`
              }}>
              {this.props.items.map(function(item,activeIndex){
                return (
                  <div className="react-swipe-views-item" key={activeIndex} style={{ width:`${100/this.props.items.length}%`}}>
                    {this.props.itemTemplate(item,activeIndex)}
                  </div>
                )
              }.bind(this))}
            </div>
        </Swipeable>
      </div>
    );
  }
}



